  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 10px;
  padding: 10px;
  resize: none;
  color: var(--text-primary);
  min-height: 28px;
  max-height: 160px;
  font-size: 14px;
}
.chat-input-wrapper.inverted textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(0, 122, 204, 0.25);
}

.chat-input-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 6px;
}

#model-indicator {
  font-size: 12px;
  color: var(--text-secondary);
}

.send-btn {
  background: var(--primary-color);
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.2s;
}
.send-btn:hover {
  background: var(--primary-hover);
}

/* ==== ABAS COM INDICADOR ANIMADO ============= */
.tabs-header {
  position: relative;
  display: flex;
  background: var(--bg-secondary);
  border-bottom: 1px solid var(--border-color);
  overflow: hidden;
  flex-shrink: 0;
}

.tab-btn {
  flex: 1;
  padding: 16px 12px;
  background: none;
  border: none;
  color: var(--text-secondary);
  font-size: 13.5px;
  font-weight: 500;
  cursor: pointer;
  transition: color 0.2s;
  position: relative;
  z-index: 2;
}
.tab-btn span {
  position: relative;
  z-index: 2;
}
.tab-btn.active {
  color: white;
}
.tab-btn:hover:not(.active) {
  color: #ddd;
}

.tab-indicator {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 3px;
  width: 33.333%;
  background: var(--primary-color);
  transition: transform 0.35s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1;
}

/* ============= CONTEÚDO DAS ABAS ============= */
.tab-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;
}

.tab-pane {
  position: absolute;
  inset: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.25s ease;
  display: flex;
  flex-direction: column;
}
.tab-pane.active {
  opacity: 1;
  visibility: visible;
  position: relative;
}

/* ========== CHAT – ESTILO CURSOR ============= */
.chat-container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

/* Garantir que o container principal tenha altura definida */
.sidebar-container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.message {
  max-width: 92%;
  padding: 12px 16px;
  border-radius: 14px;
  line-height: 1.5;
  word-wrap: break-word;
  animation: fadeInUp 0.3s ease-out;
}
.message.user {
  align-self: flex-end;
  background: var(--primary-color);
  color: white;
  border-bottom-right-radius: 4px;
}
.message.assistant {
  align-self: flex-start;
  background: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  border-bottom-left-radius: 4px;
}
.message.system {
  align-self: center;
  background: transparent;
  color: var(--text-muted);
  font-size: 13px;
  font-style: italic;
  padding: 8px 0;
}

/* scrollbar styling */
.message::-webkit-scrollbar {
  width: 10px;
}

.message::-webkit-scrollbar-track {
  background: transparent;
}

.message::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.06), rgba(255, 255, 255, 0.03));
  border-radius: 10px;
  border: 2px solid transparent;
  background-clip: padding-box;
}

/* no topo acima das respostas */
.chat-input-wrapper {
  padding: 16px;
  background: var(--bg-primary);
  border-top: 1px solid var(--border-color);
  flex-shrink: 0;
}

.chat-input-container {
  display: flex;
  align-items: flex-end;
  gap: 12px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  padding: 10px 12px;
  transition: all 0.2s ease;
}
.chat-input-container:focus-within {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(0, 122, 204, 0.25);
}

#chat-input {
  flex: 1;
  background: transparent;
  border: none;
  outline: none;
  color: var(--text-primary);
  font-size: 14px;
  resize: none;
  min-height: 24px;
  max-height: 120px;
  line-height: 1.5;
}

.send-btn {
  background: var(--primary-color);
  color: white;
  border: none;
  width: 38px;
  height: 38px;
  border-radius: 10px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  flex-shrink: 0;
}
.send-btn:hover {
  background: var(--primary-hover);
  transform: scale(1.05);
}
.send-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* ============= TOGGLE SWITCH ============= */
.toggle-switch {
  position: relative;
  display: inline-block;
  width: 46px;
  height: 26px;
}
.toggle-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.toggle-switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #444;
  transition: 0.3s;
  border-radius: 34px;
}
.toggle-switch .slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 3px;
  bottom: 3px;
  background: white;
  transition: 0.3s;
  border-radius: 50%;
}
.toggle-switch input:checked + .slider {
  background: var(--primary-color);
}
.toggle-switch input:checked + .slider:before {
  transform: translateX(20px);
}

.rule-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 0;
  border-bottom: 1px solid var(--border-color);
}
.rule-item:last-child {
  border-bottom: none;
}
.rule-item span {
  font-size: 13.5px;
  color: var(--text-primary);
}

/* ============= RULES & CONFIG ============= */
.rules-content,
.config-content {
  overflow-y: auto;
}
.rules-content::-webkit-scrollbar,
.config-content::-webkit-scrollbar {
  width: 6px;
}
.rules-content::-webkit-scrollbar-track,
.config-content::-webkit-scrollbar-track {
  background: transparent;
}
.rules-content::-webkit-scrollbar-thumb,
.config-content::-webkit-scrollbar-thumb {
  background: #555;
  border-radius: 3px;
}
.rules-content::-webkit-scrollbar-thumb:hover,
.config-content::-webkit-scrollbar-thumb:hover {
  background: #777;
}
.rules-content,
.config-content {
  padding: 8px;
}

.config-section {
  margin-bottom: 20px;
}

.config-section label {
  display: block;
  margin-bottom: 6px;
  font-size: 13px;
  color: var(--text-secondary);
}

.config-section input,
.config-section select,
.config-section textarea {
  width: 100%;
  padding: 10px 12px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  color: var(--text-primary);
  font-size: 13.5px;
}
.config-section input:focus,
.config-section select:focus,
.config-section textarea:focus {
  outline: none;
  border-color: var(--primary-color) !important;
  box-shadow: 0 0 0 2px rgba(0, 122, 204, 0.3);
}

.config-section small {
  font-size: 11.5px;
  color: var(--text-muted);
  display: block;
  margin-top: 4px;
}

.config-actions {
  display: flex;
  gap: 12px;
  margin-top: 24px;
}

/* ============= BOTÕES GERAIS ============= */
.btn-primary,
.btn-secondary {
  padding: 10px 16px;
  border: none;
  border-radius: 8px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  font-size: 13.5px;
}

.btn-primary {
  background: var(--primary-color);
  color: white;
  flex: 1;
}
.btn-primary:hover {
  background: var(--primary-hover);
}

.btn-secondary {
  background: var(--bg-tertiary);
  color: var(--text-primary);
}
.btn-secondary:hover {
  background: #3a3a3d;
}

/* ============= ANIMAÇÕES ============= */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
/* Responsividade */
@media (max-width: 480px) {
  .tab-btn {
    padding: 14px 8px;
    font-size: 13px;
  }
  .chat-input-container {
    padding: 8px;
  }
}
/* Fallback se os temas não carregarem */
.code-block {
  background: #1e1e1e;
  border-radius: 6px;
  margin: 10px 0;
  overflow: hidden;
}

.code-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 12px;
  background: #2d2d2d;
  border-bottom: 1px solid #444;
}

.code-language {
  font-size: 12px;
  color: #888;
  font-weight: 500;
  font-family: monospace;
}

pre code {
  display: block;
  padding: 12px;
  overflow-x: auto;
  font-family: "Monaco", "Menlo", "Ubuntu Mono", monospace;
  font-size: 13px;
  line-height: 1.4;
  color: #f8f8f2;
}

/* Cores básicas para fallback */
.hljs-keyword {
  color: #569cd6;
}

.hljs-string {
  color: #ce9178;
}

.hljs-function {
  color: #dcdcaa;
}

.hljs-number {
  color: #b5cea8;
}

.hljs-comment {
  color: #6a9955;
}
